/**
 * Note by Oasiris:
 * 
 * Fontello is a tool to quickly pack vector images into webfonts. Consider it 
 * like Twitter Bootstrap, but for images. It builds everything necessary to 
 * include graphics into your web pages. Also, fontello has a big built-in 
 * collection of open source artworks.
 */

@font-face {
  font-family: 'fontello';
  src: url('../fonts/fontello.eot?89674733');
  src: url('../fonts/fontello.eot?89674733#iefix') format('embedded-opentype'),
       url('../fonts/fontello.woff2?89674733') format('woff2'),
       url('../fonts/fontello.woff?89674733') format('woff'),
       url('../fonts/fontello.ttf?89674733') format('truetype'),
       url('../fonts/fontello.svg?89674733#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
