@import url("https://fonts.googleapis.com/css?family=Nunito+Sans");
body {
  margin: 0;
  padding: 0;
  font-family: "Calamity", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
