.grow {
  -webkit-transition: -webkit-transform .05s ease-in-out;
  transition: -webkit-transform .05s ease-in-out;
  transition: transform .05s ease-in-out;
  transition: transform .05s ease-in-out, -webkit-transform .05s ease-in-out; }

.grow:hover {
  -webkit-transform: scale(1.065);
          transform: scale(1.065); }

.grow-slight {
  -webkit-transition: -webkit-transform .05s ease-in-out;
  transition: -webkit-transform .05s ease-in-out;
  transition: transform .05s ease-in-out;
  transition: transform .05s ease-in-out, -webkit-transform .05s ease-in-out; }

.grow-slight:hover {
  -webkit-transform: scale(1.035);
          transform: scale(1.035); }

.text-pop {
  color: #ffd900; }

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

button {
  padding: 20px 26.25px;
  font-family: "Calamity", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: bold;
  font-size: 20px;
  border: 0;
  border-radius: 2px;
  background: rgba(5, 5, 5, 0.725);
  color: white;
  -webkit-transition: background-color 0.1s, box-shadow 0.2s;
  transition: background-color 0.1s, box-shadow 0.2s; }
  button:hover {
    background: rgba(5, 5, 5, 0.825);
    box-shadow: 0 0 15px #ffffe9; }
  button:active {
    background: rgba(255, 255, 255, 0.7);
    -webkit-transition: background-color 0s;
    transition: background-color 0s; }

