  to {
    box-shadow: 0 0 calc(4px * 0.5) rgba(255, 255, 255, 0.5), 0 0 calc(4px * 1.0) rgba(255, 255, 255, 0.5), 0 0 calc(4px * 1.5) rgba(255, 255, 255, 0.5), 0 0 calc(4px * 2.0) rgba(228, 215, 141, 0.5), 0 0 calc(4px * 3.5) rgba(228, 215, 141, 0.5), 0 0 calc(4px * 4.0) rgba(228, 215, 141, 0.5), 0 0 calc(4px * 5.0) rgba(228, 215, 141, 0.5), 0 0 calc(4px * 7.5) rgba(228, 215, 141, 0.5); } }

@-webkit-keyframes neon-passive {
