  to {
    box-shadow: 0 0 calc(2.75px * 0.5) rgba(255, 255, 255, 0.35), 0 0 calc(2.75px * 1.0) rgba(255, 255, 255, 0.35), 0 0 calc(2.75px * 1.5) rgba(255, 255, 255, 0.35), 0 0 calc(2.75px * 2.0) rgba(228, 215, 141, 0.35), 0 0 calc(2.75px * 3.5) rgba(228, 215, 141, 0.35), 0 0 calc(2.75px * 4.0) rgba(228, 215, 141, 0.35), 0 0 calc(2.75px * 5.0) rgba(228, 215, 141, 0.35), 0 0 calc(2.75px * 7.5) rgba(228, 215, 141, 0.35); } }

.card-outer, .fancy-card-outer, .selected-fancy-card-outer {
  margin: 0;
  padding: 1px;
  display: flex;
  height: 100%;
  position: relative; }

.card-inner, .simple-card-inner, .fancy-card-inner, .selected-fancy-card-inner {
  background-color: rgba(5, 5, 5, 0.725);
  color: white;
  margin: 0;
  display: inline-block; }

.fancy-card-outer, .selected-fancy-card-outer {
  border: 2px solid rgba(5, 5, 5, 0.725);
  border-radius: 3px; }

.angel-on-hover:hover {
  -webkit-animation: neon1 1.275s ease-in-out infinite alternate;
          animation: neon1 1.275s ease-in-out infinite alternate; }

.simple-card-inner {
  border-radius: 1px; }

.selected-fancy-card-outer {
  border-color: rgba(255, 252, 236, 0.725);
  -webkit-animation: neon-passive 1.5s ease-in-out infinite alternate;
          animation: neon-passive 1.5s ease-in-out infinite alternate; }

.selected-fancy-card-inner {
  background-color: rgba(255, 252, 236, 0.725);
  color: black; }

.clickable-fancy-card {
  cursor: pointer; }
  .clickable-fancy-card:active {
    border-color: rgba(255, 255, 255, 0.875);
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: -webkit-transform 0s;
    transition: -webkit-transform 0s;
    transition: transform 0s;
    transition: transform 0s, -webkit-transform 0s; }
    .clickable-fancy-card:active > div.fancy-card-inner, .clickable-fancy-card:active > div.selected-fancy-card-inner {
      background-color: rgba(255, 255, 255, 0.875); }

.small-counter-corner {
  position: absolute;
  bottom: -4px;
  right: -4px; }

.small-counter-outer {
  color: yellow;
  display: inline-block; }

.small-counter {
