.container {
  max-width: 1000px;
  margin: 10px auto;
  min-height: 80vh; }

.App {
  text-align: center; }
  .App .App-header {
    background-color: rgba(5, 5, 5, 0.725);
    border-bottom: 2px double rgba(5, 5, 5, 0.725);
    padding: 20px;
    color: white; }
    .App .App-header .App-logo {
